import React from "react";

import { withClickAnalytics } from "hocs/analytics";
import { StyledSignUpReferenceLink } from "./elements";
import CompanyTypes from "utils/companyTypes";
interface Props {
  analyticsPath: string | undefined;
  children: React.ReactNode;
  refCode: string | undefined;
  companyType?: CompanyTypes[keyof CompanyTypes];
}

const ApplyButton = ({
  analyticsPath,
  children,
  refCode,
  companyType,
}: Props) => {
  const AnalyticsApplyButton = withClickAnalytics(
    "Button",
    analyticsPath,
  )(StyledSignUpReferenceLink);

  return (
    <AnalyticsApplyButton refCode={refCode} companyType={companyType}>
      {children}
    </AnalyticsApplyButton>
  );
};

export default ApplyButton;
