import styled from "@emotion/styled";
import { padding } from "polished";
import { breakPoints } from "components/Grid.tsx";

export const Container = styled("div")({
  ...padding(88, 0),
  display: "flex",
  flexDirection: "column",
  justifyContent: "flex-start",

  [breakPoints.medium]: {
    ...padding(60, 0),
  },
});

export const List = styled("div")(({ count }) => ({
  display: "grid",
  width: "100%",
  gridTemplateColumns: "repeat(auto-fit, minmax(250px, 1fr))",
  gridColumnGap: 60,
  gridRowGap: 48,

  // this is a very non-ideal band-aid solution
  // hopefully we don't do much more than 3 or 4 value props
  [count === 4 && "@media(max-width: 1480px)"]: {
    gridTemplateColumns: "repeat(2, minmax(250px, 1fr))",
  },

  [breakPoints.medium]: {
    gridTemplateColumns: "1fr",
  },
}));
