import React from "react";

import {
  Container,
  LogoWrapper,
  Logo,
  QuoteWrapper,
  Quote,
  Author,
  AuthorTitle,
} from "./elements";

const WideTestimonial = ({ testimonial, logo = true }) => (
  <Container>
    {logo && (
      <LogoWrapper>
        <Logo
          src={testimonial.logo.file}
          height={testimonial.logo.height}
          width={testimonial.logo.width}
        />
      </LogoWrapper>
    )}
    <QuoteWrapper>
      <Quote>{testimonial.quote}</Quote>
      <Author>
        {testimonial.author.name}
        ,&nbsp;
        <AuthorTitle>{testimonial.author.title}</AuthorTitle>
      </Author>
    </QuoteWrapper>
  </Container>
);

export default WideTestimonial;
