import React from "react";

import AlternatingDetail from "components/reusable/organisms/AlternatingDetail";
import SpacerRow from "components/rows/SpacerRow";
import WideTestimonial from "components/WideTestimonial";
import { Wrapper } from "./elements";

const AlternatingDetailList = ({
  details,
  firstDetailIsImageFirstDesktop, // by default, first detail is text first on desktop
  imageFirstMobile, // by default, all details are text first on mobil
  testimonialSection = false,
  testimonial,
  notUseGradient = false,
}) => (
  <React.Fragment>
    {details.map((detailProps, index) => (
      <React.Fragment key={detailProps.title}>
        <AlternatingDetail
          notUseGradient={notUseGradient}
          imageFirstDesktop={
            index % 2 === (firstDetailIsImageFirstDesktop ? 0 : 1)
          }
          imageFirstMobile={imageFirstMobile}
          {...detailProps}
        />
        {index < details.length - 1 && <SpacerRow />}
        {testimonialSection && index === 0 && (
          <Wrapper>
            <WideTestimonial testimonial={testimonial} />
          </Wrapper>
        )}
      </React.Fragment>
    ))}
  </React.Fragment>
);

export default AlternatingDetailList;
