export const Container = styled("div")(
  ({ imageFirstDesktop, darkBackground, notUseGradient }) => {
    const whiteBackgroundWidth = "41.666666%"; // 5/12
    const backgroundColor = darkBackground ? colors.black : colors.offWhite;
    const gradient = `linear-gradient(${
      imageFirstDesktop ? "to right" : "to left"
    }, ${
      colors.white
    } ${whiteBackgroundWidth}, ${backgroundColor} ${whiteBackgroundWidth})`;

    return {
      display: "flex",
      alignItems: "flex-start",
      ...padding(60, 0),
      background: notUseGradient ? colors.white : gradient,

      [breakPoints.medium]: {
        background: notUseGradient ? colors.white : backgroundColor,
      },

      [breakPoints.small]: {
        ...padding(60, 0, 0, 0),
      },
    };
  },
);

