import React from "react";

import { Container, Icon, Title, Body, FlexWrapper } from "./elements";

const IconValueProp = ({
  icon,
  title,
  body,
  lightText,
  centered,
  flexIconsAndTitle,
}) => (
  <Container centered={centered}>
    <FlexWrapper flexIconsAndTitle={flexIconsAndTitle}>
      <Icon src={icon} />
      <div>
        <Title
          flexIconsAndTitle={flexIconsAndTitle}
          lightText={lightText}
          centered={centered}
        >
          {title}
        </Title>
        <Body flexIconsAndTitle={flexIconsAndTitle} lightText={lightText}>
          {body}
        </Body>
      </div>
    </FlexWrapper>
  </Container>
);

export default IconValueProp;
