import React from "react";
import { Row, Column } from "components/Grid.tsx";
import InternalLinkPrimaryButton from "components/reusable/atoms/buttons/InternalLinkPrimaryButton";
import ExternalLinkPrimaryButton from "components/reusable/atoms/buttons/ExternalLinkPrimaryButton";
import { ButtonType } from "components/reusable/atoms/buttons/constants";
import arrowIcon from "assets/img/icons/orange/arrow-right.svg";

import {
  Container,
  DetailText,
  Title,
  Body,
  FeatureListContainer,
  FeatureListTitle,
  FeatureList,
  Feature,
  FeatureBullet,
  FeatureText,
  ArrowLink,
  ArrowContainer,
  ButtonContainer,
  StyledLink,
  ImageContainer,
  Image,
} from "./elements";

const FeatureListComponent = ({ title, features }) => (
  <FeatureListContainer>
    <FeatureListTitle>{title}</FeatureListTitle>
    <FeatureList>
      {features.map((feature) => (
        <Feature key={feature}>
          <FeatureBullet>{"\u2022"}</FeatureBullet>
          <FeatureText>{feature}</FeatureText>
        </Feature>
      ))}
    </FeatureList>
  </FeatureListContainer>
);

const ArrowLinkComponent = ({ url, text }) => (
  <ArrowLink>
    <ArrowContainer>
      <img src={arrowIcon} alt="arrow icon" />
    </ArrowContainer>
    <StyledLink to={url}>{text}</StyledLink>
  </ArrowLink>
);

const ButtonComponent = ({ url, text, type, size }) => (
  <ButtonContainer>
    {type === ButtonType.INTERNAL_LINK_PRIMARY ? (
      <InternalLinkPrimaryButton to={url} size={size}>
        {text}
      </InternalLinkPrimaryButton>
    ) : (
      <ExternalLinkPrimaryButton href={url} size={size}>
        {text}
      </ExternalLinkPrimaryButton>
    )}
  </ButtonContainer>
);

const ImageComponent = ({
  imageColumns,
  marginOverride,
  defaultSrc,
  mobile = defaultSrc,
  mobileRetina = defaultSrc,
  desktop = defaultSrc,
  video = false,
  customWidth = "100%",
}) => (
  <ImageContainer marginOverride={marginOverride}>
    {video || (
      <Image
        customWidth={customWidth}
        src={defaultSrc}
        srcSet={`
        ${mobile.path} ${mobile.width}w,
        ${mobileRetina.path} ${mobileRetina.width}w,
        ${desktop.path} ${desktop.width}w,
      `}
        sizes={`
        (max-width: 991px) 100vw,
        (max-width: 1680px) ${(100 * imageColumns) / 12}vw,
        ${(1680 * imageColumns) / 12}px
      `}
      />
    )}
  </ImageContainer>
);

const AlternatingDetail = ({
  title,
  body,
  featureList,
  link,
  button,
  textColumns = 4,
  imageColumns = 5,
  imgSrc,
  imageMarginOverride,
  imageFirstDesktop = false,
  imageFirstMobile = false,
  darkBackground = false,
  notUseGradient,
  video,
}) => (
  <Container
    imageFirstDesktop={imageFirstDesktop}
    darkBackground={darkBackground}
    notUseGradient={notUseGradient}
  >
    <Row
      padding={30}
      gutterWidth={26}
      reversedFlexDirection={imageFirstDesktop}
      reversedFlexWrap={imageFirstMobile}
      verticalAlignment="stretch"
    >
      <Column span={1} spanMedium={0} />
      <Column span={textColumns} spanMedium={12}>
        <DetailText>
          <Title lightText={darkBackground}>{title}</Title>
          <Body lightText={darkBackground}>{body}</Body>
          {featureList && FeatureListComponent(featureList)}
          {link && ArrowLinkComponent(link)}
          {button && ButtonComponent(button)}
        </DetailText>
      </Column>
      {/*
        there may or may not be extra columns of space,
        but can't be negative
      */}
      <Column
        span={Math.max(10 - textColumns - imageColumns, 0)}
        spanMedium={0}
      />
      <Column span={imageColumns} spanMedium={12}>
        <ImageComponent
          video={video}
          marginOverride={imageMarginOverride}
          imageColumns={imageColumns}
          {...imgSrc}
        />
      </Column>
      <Column span={1} spanMedium={0} />
    </Row>
  </Container>
);

export default AlternatingDetail;
