import React from "react";

import { Row, Column } from "components/Grid.tsx";
import IconValueProp from "components/reusable/molecules/IconValueProp";
import { Container, List } from "./elements";

const IconValuePropList = ({
  valueProps,
  lightText = false,
  centered = false,
  flexIconsAndTitle = false,
}) => (
  <Container>
    <Row padding={30} gutterWidth={26}>
      <Column span={1} spanMedium={0} />
      <Column span={10} spanMedium={12}>
        <List count={valueProps.length}>
          {valueProps.map((valueProp) => (
            <IconValueProp
              flexIconsAndTitle={flexIconsAndTitle}
              centered={centered}
              key={valueProp.title}
              count={valueProps.length}
              {...valueProp}
              lightText={lightText}
            />
          ))}
        </List>
      </Column>
      <Column span={1} spanMedium={0} />
    </Row>
  </Container>
);

export default IconValuePropList;
