import React from "react";
import styled from "@emotion/styled";

const Destination = styled("div")(({ headerOffset }) => ({
  // this is necessary for hash links so that
  // it doesn't overlap with the header
  // https://paper.dropbox.com/doc/Hash-link-fix--AiXCb2wzbNmET5q7QoKXlhxKAQ-sZaArbmR7hcDsXSA0Q90e
  paddingTop: headerOffset,
  marginTop: -1 * headerOffset,
}));

const HashLinkDestination = ({ id, headerOffset, children }) => (
  <Destination headerOffset={headerOffset} id={id}>
    {children}
  </Destination>
);

export default HashLinkDestination;
