import styled from "@emotion/styled";
import colors from "styles/colors";
import { breakPoints } from "components/Grid.tsx";

const SpacerRow = styled("div")(
  ({
    height = 128,
    heightMedium = 0,
    heightSmall,
    backgroundColor = colors.white,
  }) => [
    {
      width: "100%",
      height,
      backgroundColor,
    },
    heightMedium !== undefined && {
      [breakPoints.medium]: {
        height: heightMedium,
      },
    },
    heightSmall !== undefined && {
      [breakPoints.small]: {
        height: heightSmall,
      },
    },
  ],
);

export default SpacerRow;
