import React from "react";

import { ButtonType } from "components/reusable/atoms/buttons/constants";
import ApplyButton from "components/reusable/atoms/buttons/ApplyButton";
import InternalLinkPrimaryButton from "components/reusable/atoms/buttons/InternalLinkPrimaryButton";
import InternalLinkSecondaryButton from "components/reusable/atoms/buttons/InternalLinkSecondaryButton";
import ExternalLinkPrimaryButton from "components/reusable/atoms/buttons/ExternalLinkPrimaryButton";
import ExternalLinkSecondaryButton from "components/reusable/atoms/buttons/ExternalLinkSecondaryButton";
import { Row, Column } from "components/Grid.tsx";
import {
  TitleWrapper,
  Primary,
  SubtitleWrapper,
  ButtonGroup,
  ContentWrapper,
  Content,
  Image,
  Container,
} from "./elements";

const Title = ({ titleContent }) => (
  <TitleWrapper>
    {titleContent.map((content) =>
      content.type === "primary" ? (
        <Primary key={content.text}>{content.text}</Primary>
      ) : (
        <span key={content.text}>{content.text}</span>
      ),
    )}
  </TitleWrapper>
);

const Subtitle = ({ subtitleContent }) => (
  <SubtitleWrapper>
    {subtitleContent.map((content) =>
      content.type === "primary" ? (
        <Primary key={content.text}>{content.text}</Primary>
      ) : (
        <span key={content.text}>{content.text}</span>
      ),
    )}
  </SubtitleWrapper>
);

const SimpleHero = ({
  titleContent,
  subtitleContent,
  ctaList,
  imgSrc,
  fullHeightImg = false,
}) => (
  <Container>
    <Row padding={30} gutterWidth={26}>
      <Column span={1} spanMedium={0} />
      <Column span={11} spanMedium={12}>
        <ContentWrapper>
          <Content>
            <Title titleContent={titleContent} />
            {subtitleContent && <Subtitle subtitleContent={subtitleContent} />}
            <ButtonGroup>
              {ctaList.map(
                ({ type, text, url, companyType, analyticsPath, refCode }) => {
                  if (type === ButtonType.APPLY) {
                    return (
                      <ApplyButton
                        key={text}
                        companyType={companyType}
                        analyticsPath={analyticsPath}
                        refCode={refCode}
                      >
                        {text}
                      </ApplyButton>
                    );
                  } else if (type === ButtonType.INTERNAL_LINK_PRIMARY) {
                    return (
                      <InternalLinkPrimaryButton key={text} to={url}>
                        {text}
                      </InternalLinkPrimaryButton>
                    );
                  } else if (type === ButtonType.INTERNAL_LINK_SECONDARY) {
                    return (
                      <InternalLinkSecondaryButton key={text} to={url}>
                        {text}
                      </InternalLinkSecondaryButton>
                    );
                  } else if (type === ButtonType.EXTERNAL_LINK_PRIMARY) {
                    return (
                      <ExternalLinkPrimaryButton key={text} href={url}>
                        {text}
                      </ExternalLinkPrimaryButton>
                    );
                  } else if (type === ButtonType.EXTERNAL_LINK_SECONDARY) {
                    return (
                      <ExternalLinkSecondaryButton key={text} href={url}>
                        {text}
                      </ExternalLinkSecondaryButton>
                    );
                  } else {
                    return null;
                  }
                },
              )}
            </ButtonGroup>
          </Content>
          <Image src={imgSrc} fullHeightImg={fullHeightImg} />
        </ContentWrapper>
      </Column>
    </Row>
  </Container>
);

export default SimpleHero;
